.intro {
    display: flex;
    height: 100vh;
}

.intro-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;

}

.GitHub2 {
    font-size: 40px !important;
    color: rgb(211, 99, 155);
    margin-right: 10px;
}

.LinkedIn {
    font-size: 40px !important;
    color: rgb(211, 99, 155);
}


.i-scroll {
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
}

.intro-right {
    flex: 1;
    position: relative;
}

.intro-left-wrapper {
    padding: 50px;
    height: 50%;
    /* border: solid pink; */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.intro-hello {
    font-size: 35px;

}

.intro-name {
    font-size: 65px;
    font-weight: 400;
    font-weight: lighter;
    /* color: rgb(73, 233, 166); */
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.503);

}

.intro-title {
    height: 50px;
    overflow: hidden;
}

.intro-title-wrapper {
    height: 100%;
    animation: move 7s ease-in-out infinite alternate;
}

@keyframes move {
    33% {
        transform: translateY(-50px);
    }

    66% {
        transform: translateY(-100px);
    }

    100% {
        transform: translateY(-150px);
    }

}

.intro-title-item {
    height: 50px;
    font-size: 25px;
    font-weight: bold;
    color: rgb(141, 173, 220);
    display: flex;
    align-items: center;
}

.intro-title-item2 {
    height: 50px;
    font-size: 25px;
    font-weight: bold;
    color: rgb(27, 126, 197);
    display: flex;
    align-items: center;
}

.intro-description {
    font-size: 20px;
}


.intro-bg {
    /* clip-path: polygon(100% 0%, 65% 49%, 100% 100%, 25% 100%, 0% 50%, 34% 0); */
    clip-path: circle(47.4% at 92% 42%);
    clip-path: polygon(100% 0%, 75% 50%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-image: linear-gradient(180deg, #aae1ff, #dbe4f2, #86a8da, #ffffff);
    background-image: linear-gradient(180deg, #f8c6e1, #f2dbef, #d7a6d8, #ffffff, #525e64);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}


.intro-image {
    width: 100%;
    height: 100%;
    object-fit: contain;
    position: absolute;
}


@media screen and (max-width: 480px) {
    .intro {
        flex-direction: column;
    }
    .intro-left-wrapper {
        padding: 10px;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 100%;
    }
    .intro-description{
        display: none;
    }
}