.contact {
    height: 100vh;
    position: relative;
}

.c-bg {
    width: 15px;
    height: 100%;
    background-image: linear-gradient(180deg, #ffffff, #b0e0fa, #b5cff9, #5785d4, #ffffff);
    position: absolute;
}

.c-wrapper {
    padding: 50px;
    display: flex;
}

.c-left {
    flex: 1;
}




form {
    margin-top: 20px;

}

input {
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: 1px solid rgb(0, 0, 0);
    margin: 10px 0px;
    font-size: 17px;
    font-weight: 500;
}

textarea {
    width: 50%;
    margin: 20px 0px;
    font-size: 17px;
    padding-left: 10px;
}

.c-button {
    margin-left: 42%;
    display: flex;
    align-items: center;
    justify-content: left;
}

button {
    border: none;
    border-radius: 7%;
    padding: 17px;
    background-color: rgb(3, 137, 209);
    color: rgb(255, 255, 255);
    font-weight: 500;
    font-size: 17px;
    cursor: pointer;
    margin-left: 15px;
    transition: transform 200ms, box-shadow 200ms;
    box-shadow: #094c66 4px 4px 4px;
}

button:active {
    transform: translateY(4px) translateX(4px);
    box-shadow: #094c66 0px 0px 0px;
    ;

}

.c-sent {
    font-size: 25px;
    font-weight: 600;
    margin-left: -20%;
    color: rgb(39, 77, 41);
}

.c-GitHub2 {
    font-size: 40px !important;
    color: rgb(211, 99, 155);
    margin-right: 10px;
    margin-top: 100px;
}

.c-LinkedIn {
    font-size: 40px !important;
    color: rgb(211, 99, 155);
}


.c-right {
    flex: 1;
    position: absolute;
    transform: translate(-50%, -50%);
    top: 30%;
    right: 1%;
    font-weight: 700;
    color: #abb4b8;
}

