@media screen and (max-width: 480px) {
    .c-wrapper {
        flex-direction: column;
        padding: 50px 50px;
    }
    .c-title {
        font-size: 30px;
        justify-content: center;
    }

    form {
        margin-top: 10px;
        display: flex;
        flex-wrap: wrap;
    }

    input {
        width: 45%;
        height: 40px;
        margin: 10px;
        margin-left: 0;
    }
    textarea{
        width: 90%;
    }
}