.toggle {
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border: 1px solid gray;
    /* background-color: lightsteelblue; */
    position: fixed;
    top: 10px;
    right: 15px;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.toggle-icon {
    width: 15px;
    height: 15px;
}

.toggle-button {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: gray;
    position: absolute;
    cursor: pointer;
}